<template>
    <div>
        <div class="picblock" :style="{top:top+'%',left:left+'%'}">
            <div class="zoomblock">
                <div class="close" @click="close">
                    <span>close</span>
                </div>
                <div class="open" @click="open">
                    <span>open</span>
                </div>
            </div>
            <div class="pic">
                <div class="date">
                    <span>{{createdate}}</span>
                </div>
                <img :class="indexofcom" :src="piclink" alt="">
            </div>
        </div>
        <div class="mobilepicblock">
    
        </div>
    </div>
</template>

<script>
import anime from 'animejs/lib/anime.es.js';
export default {
    props: ['projectpic', 'topprops', 'leftprops', 'mobileleftprops', 'mobiletopprops', 'projectlink','clickcom','date'],
    data() {
        return {
            top: this.topprops,
            left: this.leftprops,
            piclink:this.projectlink,
            indexofcom:'pic'+this.clickcom,
            createdate:this.date

        }
    },
    methods: {
        close() {
            anime({
                targets: `.pic > .${this.indexofcom}`,
                scale: 0,
                easing: 'easeInOutQuad'
            });
        },
        open() {
            anime({
                targets: `.pic > .${this.indexofcom}`,
                scale: 1,
                easing: 'easeInOutQuad'
            });
        }
    },
}
</script>

