<script>
import headerbar from '../components/menu'
import introblock from '../components/introblock'
import picblock from '../components/picblock'
import Moveable from 'vue-moveable';
import threeplayground from '../components/threeplayground'
import projectblock from '../components/projectblock'
import Useblock from '../components/Useinfo'
// import videoblock from '../components/video'
// import firebase from 'firebase/app'
// import "firebase/storage";
export default {
    name: 'Home',

    data() {
        return {
            currentindex: 0,
            indextest: '5',
            isActive: false,
            lasttarget: null,
            isclick: false,
            moveable: {
                draggable: true,
                throttleDrag: 0,
                throttleDragRotate: 0,

            }
        }
    },
    mounted() {
        window.addEventListener('resize', () => {
            if (document.body.clientWidth <= 768) {
                this.moveable.draggable = false;      
            } else {
                this.moveable.draggable = true
            }
        })

        // console.log(this.db)

    },
    methods: {
        changeindex(event) {
            let currentclass = event.currentTarget.classList[1]

            let el = document.querySelector('.currentfront')
            //點擊如果是null就加上index
            if (el == null) {
                event.currentTarget.className += " currentfront"
                this.lasttarget = 'currentfront';
            } else if (this.lasttarget == currentclass && el != null) {
                // continue;
                // console.log(el, this.lasttarget, '點到一樣的!', currentclass)
            } else {
                el.classList.remove('currentfront')
                event.currentTarget.className += " currentfront"
            }

        },

        handleDrag({
            target,
            transform
        }) {
            target.style.zIndex = '-1'
            target.style.transform = transform;
            // console.log(target)
        },
        handleResize({
            target,
            width,
            height,
            delta,
        }) {

            delta[0] && (target.style.width = `${width}px`);
            delta[1] && (target.style.height = `${height}px`);
        },
        handleScale({
            target,
            transform,
            scale
        }) {
            console.log('onScale scale', scale);
            target.style.transform = transform;
        },
        handleRotate({
            target,
            dist,
            transform
        }) {
            console.log('onRotate', dist);
            target.style.transform = transform;
        },
        handleWarp({
            target,
            transform
        }) {
            console.log('onWarp', transform);
            target.style.transform = transform;
        },
        handlePinch({
            target
        }) {
            console.log('onPinch', target);
        },
    },

    components: {
        introblock,
        Moveable,
        picblock,
        threeplayground,
        // motionticket,
        headerbar,
        projectblock,
        Useblock
    }
}
</script>

