<template>
<div class="palyground">
    <canvas id="three"></canvas>
</div>
</template>

<script>
import * as THREE from 'three'
// import * as dat from 'dat.gui'
export default {
    data() {
        return {

        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            const textureloader = new THREE.TextureLoader();
            const normaltexture = textureloader.load(require('../assets/textures/NormalMap.png'));

            // Canvas
            // console.log(normaltexture)
            const canvas = document.querySelector('#three')
            console.log('高度', canvas.offsetHeight);
            // Scene
            const scene = new THREE.Scene()

            // Objects
            // const geometry = new THREE.TorusGeometry(.7, .2, 16, 100);
            const geometry = new THREE.SphereBufferGeometry(0.7, 64, 64)

            const material = new THREE.MeshStandardMaterial()
            material.metalness = 0.8
            material.roughness = 0.1
            material.normalMap = normaltexture;
            material.color = new THREE.Color("rgb(124, 66, 60)")

            // Mesh
            const sphere = new THREE.Mesh(geometry, material)

            scene.add(sphere)

            // Lights

            const pointLight = new THREE.PointLight(0xffffff, 1)
            pointLight.position.x = 2
            pointLight.position.y = 3
            pointLight.position.z = 4
            scene.add(pointLight)

            const pointLight2 = new THREE.PointLight(0xff0000, 2)
            pointLight2.position.set(-2.25, -0.87, 2.39)
            pointLight2.intensity = 1.39
            scene.add(pointLight2)

            const pointlightHelper = new THREE.PointLightHelper(pointLight2, 1)

            scene.add(pointlightHelper)

            /**
             * Sizes
             */
            const sizes = {
                width: canvas.offsetWidth,
                height: canvas.offsetHeight
            }

            // window.addEventListener('resize', () => {
            //     // Update sizes
            //     sizes.width = window.innerWidth
            //     sizes.height = window.innerHeight

            //     // Update camera
            //     camera.aspect = sizes.width / sizes.height
            //     camera.updateProjectionMatrix()

            //     // Update renderer
            //     renderer.setSize(sizes.width, sizes.height)
            //     renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
            // })

            /**
             * Camera
             */
            // Base camera
            const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
            camera.position.x = 0
            camera.position.y = 0
            camera.position.z = 2
            scene.add(camera)

            // Controls
            // const controls = new OrbitControls(camera, canvas)
            // controls.enableDamping = true

            /**
             * Renderer
             */
            const renderer = new THREE.WebGLRenderer({
                canvas: canvas,
                alpha: true
            })
            renderer.setSize(sizes.width, sizes.height)
            renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

            /**
             * Animate
             * 
             */

            document.querySelector('.palyground').addEventListener('mousemove', movethecircle)

            let mouseX = 0
            let mouseY = 0

            let targetX = 0
            let targetY = 0

            const windowHalfX = window.innerWidth / 2;
            const windowHalfY = window.innerHeight / 2;

            function movethecircle(e) {
                mouseX = (e.clientX - windowHalfX);
                mouseY = (e.clientY - windowHalfY);

            }

            const clock = new THREE.Clock()

            const tick = () => {

                targetX = mouseX * .003
                targetY = mouseY * .003

                // console.log(targetX,targetY)
                const elapsedTime = clock.getElapsedTime()

                // Update objects
                sphere.rotation.y = .5 * elapsedTime

                sphere.rotation.y += .5 * (targetX - sphere.rotation.y)
                sphere.rotation.x += .5 * (targetY - sphere.rotation.x)
                sphere.position.z += .5 * (targetY - sphere.rotation.x)
                // Update Orbital Controls
                // controls.update()

                // Render
                renderer.render(scene, camera)

                // Call tick again on the next frame
                window.requestAnimationFrame(tick)
            }

            tick();
        }
    }

}
</script>

<style lang="scss" scoped>
@media (max-width: 767px) {
    #three {
        display: none;
    }
}

#three {
    width: 400px;
    height: 400px;
    background-color: rgb(31, 31, 31);
    border-radius: 5px;
    border: solid 1px;
}
</style>
